h1 {
  color: green;
}
.notes {
  color: grey;
  padding-top: 3px;
  font-size: 15px;
}
.notification {
  font-size: 20px;
  padding: 10px;
  border: 1px solid black;
  width: max-content;
  margin-bottom: 10px;
  border-radius: 5px;
}
.error {
  color: red;
  border-color: red;
}
.success {
  color: green;
  border-color: green;
}